Drupal.behaviors.tabs = {
  attach(context) {
    const tabContainers = context.querySelectorAll('.tabs'); // Encuentra cada conjunto de tabs.

    // Itera sobre cada conjunto de tabs para manejarlas de forma independiente.
    tabContainers.forEach((tabsContainer) => {
      const tabNavigationLinks = tabsContainer.querySelectorAll('.tabs__link');
      const tabContentContainers = tabsContainer.querySelectorAll('.tabs__tab');
      let activeIndex = 0; // Estado local para esta instancia de tabs.

      /**
       * goToTab
       *
       * @description Cambia a una pestaña específica basada en el índice. Actualiza estilos y estado.
       * @param {number} index El índice de la pestaña a mostrar
       */
      function goToTab(index) {
        if (
          index !== activeIndex &&
          index >= 0 &&
          index < tabNavigationLinks.length
        ) {
          tabNavigationLinks[activeIndex].classList.remove('is-active');
          tabNavigationLinks[index].classList.add('is-active');
          tabContentContainers[activeIndex].classList.remove('is-active');
          tabContentContainers[index].classList.add('is-active');
          activeIndex = index; // Actualiza el estado activo.
        }
      }

      /**
       * handleClick
       *
       * @description Escucha los clics en los enlaces de navegación y activa la pestaña correspondiente.
       * @param {HTMLElement} link El enlace al que se le añadirá el evento
       * @param {number} index El índice de ese enlace
       */
      function handleClick(link, index) {
        link.addEventListener('click', (e) => {
          e.preventDefault();
          goToTab(index);
        });
      }

      /**
       * init
       *
       * @description Inicializa las tabs eliminando la clase no-js y asignando eventos.
       */
      tabsContainer.classList.remove('no-js');

      tabNavigationLinks.forEach((link, index) => {
        handleClick(link, index);
      });
    });
  },
};
